<template>
  <div id="detail">
    <el-tooltip class="item" effect="dark" content="你的文件不会被保存，放心上传" placement="top-start" style="margin-bottom:10px">
      <el-upload
      class="upload-demo"
      drag
      :auto-upload="false"
			:on-change="handleChange"
      :show-file-list="false"
			accept=".xls,.xlsx">
    <em class="el-icon-upload"></em>
    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
    <div class="el-upload__tip">暂时只能解析.xls/.xlsx文件</div>
    <div class="el-upload__tip">注意：请确保表格有表头</div>
    </el-upload>
    </el-tooltip>
  <el-select id="numberSelect" v-model="value" placeholder="小组人数" style="margin-right: 10px">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <el-button id="start" type="success">开始分组</el-button>
  <el-table
    id="detailTable"
    :data="tableData"
    height="800">
    <el-table-column v-for="(item,index) in items" :key=index :prop=item.id :label=item.name width="180"></el-table-column>
    <span v-html="rawColumn"></span>
  </el-table>
  <el-button type="success" style="margin-top:15px; float: right"> 下载分组结果 </el-button>
  </div>
</template>

<script>
  export default {
    name: "ExcelDetail",
    data() {
      return {
        items:[],
        options: [{
          value: '2',
          label: '2'
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }, {
          value: '5',
          label: '5'
        }, {
          value: '6',
          label: '6'
        }, {
          value: '7',
          label: '7'
        }, {
          value: '8',
          label: '8'
        }],
        value:'',
        tableData: [],
        fileContent: '',
      }
    },
    methods: {
      handleChange (file) {
        this.items=[]
        this.tableData=[]
        this.tableData=[]
        this.fileContent = file.raw
        const fileName = file.name
        const fileType = fileName.substring(fileName.lastIndexOf('.') + 1)
        if (this.fileContent) {
          if (fileType === 'xlsx' || fileType === 'xls') {
            this.importfile(this.fileContent)
          } else {
            this.$message({
              type: 'warning',
              message: '附件格式错误，请重新上传！'
            })
          }
        } else {
            this.$message({
              type: 'warning',
              message: '请上传附件！'
            })
          }
        
      },
      async importfile (obj) {
        const reader = new FileReader()
        const _this = this
        reader.readAsArrayBuffer(obj)
        reader.onload = function () {
          const buffer = reader.result
          const bytes = new Uint8Array(buffer)
          const length = bytes.byteLength
        let binary = ''
        for (let i = 0; i < length; i++) {
          binary += String.fromCharCode(bytes[i])
        }
        const XLSX = require('xlsx')
        const wb = XLSX.read(binary, {
          type: 'binary'
        })
        const outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]],{header: 1})
        this.data = [...outdata]
        const arr = []
  //      const pointer = _this
        for(let i=0;i<this.data[0].length;i++) {
          const ob = {}
          ob.id = 'type'+i;
          ob.name = this.data[0][i];
          _this.items.push(ob);
        }
        for(let i=1,types=_this.items;i<this.data.length;i++) {
          const ob={}
          console.log(this.data[i])
          for(let j=0;j<types.length;j++) {
            ob['type'+j]=this.data[i][j]
          }
          arr.push(ob)
        }
        // this.data.map(v => {
        //   const ob = {}
        //   for(let i=0;i<pointer.items.length;i++) {
        //     console.log(pointer.items[i].t)
        //     ob.pointer.items[i].t =v.pointer.items[i].t
        //   }
        //   arr.push(ob)
        // })
        _this.tableData = _this.tableData.concat(arr)
        console.log(_this.tableData)
        console.log(_this.items)
      }
    }
  }
}
</script>

